body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #eef3fe;
}

.App {
  padding: 20px;
}

.card {
  background: #fff;
  margin: 0;
  border: none;
  box-shadow: 1px 1px 10px #dbe8fa;
  margin-bottom: 20px;
}

.top {
  padding: 15px;
}

img {
  height: 100px;
  border-radius: 50px;
  margin-top: 30px;
}

.name {
  color: #435784;
  margin-top: 20px;
  letter-spacing: 2px;
}

.job-title {
  font-size: 12px;
  color: #bfcde2;
  font-weight: 500;
  margin-bottom: 0;
}

.icon {
  font-size: 12px;
  color: #829cbb;
  margin-top: 3px;
  margin-right: 5px;
}

.description {
  display: inline-block;
}

.bottom {
  border-top: 1px solid #eee;
}

.bottom-contents {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.bottom-contents .far {
  color: #829cbb;
}

